/* eslint-disable camelcase */
/* eslint-disable react/button-has-type */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import { toast } from 'react-toastify';
// import CloseIcon from '@material-ui/icons/Close';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import SearchIcon from '@material-ui/icons/Search';
import { push } from 'react-router-redux';
import './Header.css';
// import agent from '../../agent';
// import ProfilePIc from '../../common/Components/ProfilePIc';
// import Loader from '../../../common/Components/Loader';
import { store } from '../../../store';
import {
  GET_USER_DETAILS, GET_PUBLIC_PAGE_HEADER_INFO, GET_PUBLIC_PAGE_USER, CLEAR_CART, GET_PUBLIC_PAGE_SHOP_DETAILS, GET_PUBLIC_PAGE_BG_STYLE,
} from '../../../constants/actionTypes';
import Logo from '../../../common/Components/Logo';
import { DEFAULT_BG_STYLE, DEFAULT_TEXT_COLOR } from '../../../constants/otherConstants';
// import ChangeProfilePic from '../../../common/Components/ChangeProfilePic';
// import ProductDetails from './components/ProductDetails';

import lightOrDark from '../../../common/Components/checkBgColor';

// import Image from '../../common/Components/Image';
// import RenderInfiniteScrollProduct from './components/RenderInfiniteScrollProduct';

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user: { profile_pic: null, link: [], username: '' },
      mobileView: false,
      render: false,
      shippingAddress: '',
    };

    this.cartItems = [];
  }

  componentWillMount() {
    window.addEventListener('resize', this.resize.bind(this));
    this.resize();
    // toast.info("World Wide Shipping !!!")
    const {
      parent, user, cartItems, clearCart, setPublicPageUserDetails,
    } = this.props;
    if (parent) {
      if (parent === 'admin-page') {
        if (user) {
          this.setState({ user: user.user, togelSignUpMessage: false });
          setPublicPageUserDetails({ user: user.user });
        }
      }
    }

    if (cartItems) {
      this.cartItems = cartItems;
      if (this.state.user.id) {
        if (this.cartItems.filter((i) => i.item.user !== this.state.user.id).length > 0) {
          clearCart();
        }
      }
      this.setState({ render: !this.state.render });
    }
  }

  componentWillReceiveProps(np) {
    const {
      shippingAddress, user, render, categories,
    } = this.state;
    const { clearCart, parent, setPublicPageUserDetails } = this.props;
    if (parent === 'admin-page') {
      if (np.user && np.user.user !== user) {
        this.setState({ user: np.user.user });
        setPublicPageUserDetails({ user: np.user.user });
      }
    } else {
      if (np.publicPageUser) {
        this.links = np.publicPageUser.links;
        this.setState({ user: np.publicPageUser });
      }

      if (np.publicPageShopDetails) {
        if (np.publicPageShopDetails.address !== shippingAddress) {
          this.setState({ shippingAddress: np.publicPageShopDetails.address });
          toast.info(np.publicPageShopDetails.address);
        }
      }
      if (np.cartItems && np.cartItems !== this.cartItems) {
        this.cartItems = np.cartItems;
        this.setState({ render: !render });
      }
      if (user.id && np.cartItems) {
        if (this.cartItems.filter((i) => i.item.user !== user.id).length > 0) {
          clearCart();
        }
      }
    }
  }

  resize() {
    this.setState({ mobileView: window.innerWidth <= 600 });
  }

  showCart() {
    const path = window.location.search ? `${window.location.pathname}${window.location.search}&cartDetails=true`
      : `${window.location.pathname}?cartDetails=true`;
    store.dispatch(push(path));
  }

  render() {
    const {
      user, shippingAddress, mobileView,
    } = this.state;
    const { username } = user;
    if (mobileView) {
      return (
        < >
          <nav className="home-page-nav-div">
            <div className="search-div">
              <input placeholder="Search " />
              <SearchIcon />
            </div>
            <button className="cart-div " type="button" onClick={this.showCart}>
              <ShoppingCartIcon />

              <span>
                Cart(
                {this.cartItems.length}
                )
              </span>
            </button>

          </nav>
          <header className="home-page-top-header">
            <div className="left-div">
              <Logo
                logostyles={{
                  display: 'flex', flexDirection: 'row',
                }}
                parent={!this.props.parent ? 'public-home-page' : this.props.parent}
                username={username}
              />
            </div>
            <div className="right-div">
              <div className="shipping-area">
                {`${shippingAddress}`}
              </div>
            </div>
          </header>
        </>
      );
    }
    return (
      < >
        <header className="home-page-top-header">
          <div className="left-div">
            <Logo
              logostyles={{
                display: 'flex', flexDirection: 'row', color: '#9c756f',
              }}
              parent={!this.props.parent ? 'public-home-page' : this.props.parent}
              username={username}
            />
          </div>
          <div className="right-div">
            <div className="shipping-area">
              {' '}
              {`${shippingAddress}`}
              {' '}
            </div>
            <div className="bottom-div">
              <div className="search-div">
                <input />
                <SearchIcon />
              </div>
              <button className="cart-div " type="button" onClick={this.showCart}>
                <ShoppingCartIcon />

                <span>
                  Cart(
                  {this.cartItems.length}
                  )
                </span>
              </button>
            </div>

          </div>
        </header>
        <nav className="home-page-nav-div" />
      </>
    );
  }
}


const mapStateToProps = (state) => ({
  homeBgStyle: state.AdminPageReducers.homeBgStyle,
  cartItems: state.HomePageReducers.cartItems,
  publicPageShopDetails: state.HomePageReducers.publicPageShopDetails,
});

const mapDispatchToProps = (dispatch) => ({
  clearCart: () => store.dispatch({ type: CLEAR_CART }),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Header);
