import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import { push } from 'react-router-redux';
import './PublicFooter.css';
import { store } from '../../../store';

import Logo from '../../../common/Components/Logo';

class PublicFooter extends Component {
  constructor(props) {
    super(props);
    this.state = {

      user: {},
    };
  }

  componentWillMount() {
    const {
      parent, links, user,
    } = this.props;

    if (parent) {
      if (parent === 'admin-page') {
        if (links) {
          this.links = links.links;
        }
        if (user) {
          this.setState({ user: user.user });
        }
      }
    } else {
      // other api callls

    }

    // cart APi
    // cart Items related info
  }

  componentWillReceiveProps(np) {
    const {
      user,
    } = this.state;
    const { parent } = this.props;
    if (parent === 'admin-page') {
      if (np.user && np.user.user !== user) {
        this.setState({ user: np.user.user });
      }

      this.setState({ loading: false });
    } else if (np.publicPageUser) {
      this.links = np.publicPageUser.links;
      this.setState({ user: np.publicPageUser, loading: false });
    }
  }


  render() {
    const {
      user,
    } = this.state;
    const { username } = user;
    return (
      <footer className="public-page-footer">
        <div className="tc flex flex-column items-center justify-center">
          <div className="footer-logo">
            <Logo
              logostyles={{
                display: 'flex', flexDirection: 'row', margin: 'auto', color: '#9c756f',
              }}
              parent={!this.props.parent ? 'public-home-page' : this.props.parent}
            />
          </div>
        </div>
      </footer>
    );
  }
}

const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,
  links: state.AdminPageReducers.links,
  publicPageUser: state.HomePageReducers.publicPageUser,
});
const mapDispatchToProps = (dispatch) => ({

});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(PublicFooter);
